import React, { Component } from 'react';
import './AreaElement.css'

class AreaElement extends Component{
    constructor(props){
        super()

        this.interest = parseInt(props.interest)
        this.area = props.name
    }

    buildStrokeDasharray(){
        let gap = 100 - this.interest
        return this.interest.toString() + " " + gap.toString()
    }   

    render(){

        // This code was very much inspired by the following article on donut circles SVGs:
        // https://medium.com/@heyoka/scratch-made-svg-donut-pie-charts-in-html5-2c587e935d72
        return(
            <div className="grid-element">
                <svg width="80%" height="80%" viewBox="0 0 42 42" className="donut">
                    <circle className="donut-hole" cx="21" cy="21" r="15.91549430918954" fill="transparent"></circle>
                    <circle className="donut-ring" cx="21" cy="21" r="15.91549430918954" fill="transparent" stroke="#FE5F55" strokeWidth="2"></circle>
                    <circle className="donut-segment" cx="21" cy="21" r="15.91549430918954" fill="transparent" stroke="#2E6296" strokeWidth="2" strokeDasharray={this.buildStrokeDasharray()} strokeDashoffset="25"></circle>
                    <text x="42" y="42" transform="scale(0.5, 0.5)" textAnchor="middle" stroke="#d1d1d1" strokeWidth="2px" dy=".3em">{this.interest}%</text>
                </svg>
                <h3>{this.area}</h3>
            </div>
        );
    }
}

export default AreaElement;