import React, { Component } from 'react';
import './InfoElement.css'

class InfoElement extends Component{
    constructor(props){
        super()
    }

    render(){
        return(
            <div className="info-elem-container">
                <i className={this.props.iconClass}></i>
                <p>{this.props.text}</p>
            </div>
        );
    }
}

export default InfoElement;