import React, { Component } from 'react';

class Circle extends Component{
    
    render(){
        const circleStyle = {
            height: '10px',
            width: '10px',
            borderRadius: '50%',
            backgroundColor: '#2E6296'
        };

        return(
            <div style={circleStyle}></div>
        );
    }


}

export default Circle;