import React, { Component } from 'react';
import InfoElement from './InfoElement';

import './PersonalInfo.css'

class PersonalInfo extends Component{

    getCurrentAge(){
        let birthDate = new Date(1997, 6, 29);
        let currentDate = new Date()  

        let birthDateMonth = birthDate.getMonth();
        let currentMonth = currentDate.getMonth();
        
        if(currentMonth === birthDateMonth){
            let birthDateDay = birthDate.getDate();
            let currentDay = currentDate.getDate();

            if(currentDay >= birthDateDay){
                return currentDate.getFullYear() - birthDate.getFullYear()
            } 
            return currentDate.getFullYear() - birthDate.getFullYear() - 1
        } 
        else if(currentMonth > birthDateMonth){
            return currentDate.getFullYear() - birthDate.getFullYear()
        } 
        else{
            return currentDate.getFullYear() - birthDate.getFullYear() - 1
        }
    }

    render(){
        let age = this.getCurrentAge() + " Years Old"
        return(
            <section id="personal-info-container">
                <div className="info-header">
                    <h2>About Me</h2>
                </div>
                <InfoElement iconClass="far fa-address-card" text="Francisco Maria"/>
                <InfoElement iconClass="fas fa-birthday-cake" text={age}/>
                <InfoElement iconClass="fas fa-university" text="Master's Degree in Computer Science  - FEUP (4th Year)"/>
                <InfoElement iconClass="fas fa-map-marker-alt" text="Porto, Portugal"/>
                <InfoElement iconClass="fas fa-football-ball" text="Rugby, Running, Cycling, Squash and Gym Workout"/>
                <InfoElement iconClass="far fa-envelope" text="Click me"/>
            </section>
        );
    }
}

export default PersonalInfo;