import React, { Component } from 'react';

class Thinline extends Component{
    
    render(){
        const leftLineStyle = { 
            margin: '0.25em 2em 0.25em 0',
            flex: 2,
            width: '30%',
            backgroundColor: '#2E6296'
        };

        const rightLineStyle = {
            margin: '0.25em 0 0.25em 2em',
            flex: 2,
            width: '30%',
            backgroundColor: '#2E6296'
        }

        if(this.props.side === "left"){
            return(
                <div style={leftLineStyle} className="separation-line"></div>
            );
        } else {
            return(
                <div style={rightLineStyle} className="separation-line"></div>
            );
        }

    }
}

export default Thinline;